.mat-flat-button.mat-accent {
  background-color: #ff6400;
  color: #fff;
  line-height: 40px;
  padding: 0 19px;
  border: transparent;
  margin-top: 5px;
}

.custom-error {
  color: red;
  display: block;
  /* font-size: 11px; */
  position: absolute;
  width: 65%;
  margin-top: 10px;
  text-align: center;
}

.edit-profile {
  margin: 0 auto;
  width: 88%;
}

.left_block {
  width: 78%;
}

.right_block {
  width: 100%;
}

.multuser_outer {
  max-height: calc(100vh - 540px);
  overflow: auto;
  padding-top: 38px;
}

.multuser_outer .mat-form-field {
  margin-bottom: 25px;
}

.cncl_btn {
  background: #919191 !important
}

/*responsive css start*/
@media screen and (max-width: 767px) {
  .add-user-form .multuser_outer.addbox .col {
    min-width: 32% !important;
  }

  .add-user-form .col {
    min-width: 100% !important;
  }

  .add-user-form .col:nth-child(1) {
    margin-bottom: 28px;
  }

  .add-user-form .multuser_outer .col:nth-child(1) {
    margin-bottom: 0;
  }

  .add-user-form .left_block {
    width: 100%;
  }

  .add-user-form .multuser_outer.addbox {
    max-height: calc(100vh - 540px) !important;
  }

  .multuser_outer {
    max-height: inherit !important;
    padding-top: 28px;
  }

  .custom-error {
    top: 87px;
    left: 0;
    width: 100%;
  }
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .custom-error {
    left: -66px;
    top: 91px;
  }
}

::ng-deep .mat-option-text {
  display: inline-block;
  flex-grow: 1;
  overflow: hidden;
  text-overflow: initial;
  max-width: 100%;
  white-space: initial;
  line-height: 1.3em;
}

::ng-deep .mat-select-panel .mat-option {
  height: 3.3em;
}

.right-part {
  height: 40.81px;
}